<template>
  <el-dialog
    width="900px"
    :title="title"
    :visible.sync="dialogVisible"
    :before-close="hideDialog"
    :close-on-click-modal="false"
  >
    <div class="dialogBox">
      <div class="dialog-left">
        <el-divider content-position="left">路线清单</el-divider>
        <div class="dialog-left-table">
          <div class="dialog-left-table-search">
            <el-input
              v-model="formData.lineName"
              placeholder="输入路线名称"
            ></el-input>
            <el-button
              style="margin-left: 20px"
              @click="getTableData"
              type="primary"
              icon="el-icon-search"
            >
              查询
            </el-button>
          </div>
          <el-table
            ref="singleTable"
            style="width: 100%; overflow-y: auto"
            highlight-current-row
            border
            :data="tableData"
            :height="tableDataHeight"
            :row-class-name="getRowClassName"
            show-overflow-tooltip
            @row-click="selectBorderData"
          >
            <el-table-column label="序号" type="index"> </el-table-column>
            <el-table-column
              label="路线名称"
              show-overflow-tooltip
              align="center"
              prop="lineName"
            />
            <el-table-column
              label="起点名称"
              show-overflow-tooltip
              align="center"
              prop="originName"
            />
            <el-table-column
              label="终点名称"
              show-overflow-tooltip
              align="center"
              prop="terminusName"
            />
          </el-table>
        </div>
      </div>
      <div class="dialog-right">
        <el-divider content-position="left">已选路线</el-divider>
        <div class="dialog-right-option">
          <el-tag
            v-for="(tag, index) in lineArr"
            :key="tag.id"
            closable
            @close="handleClose(index)"
          >
            <div class="dialog-right-option-tag" :title="tag.lineName">
              {{ tag.lineName }}
            </div>
          </el-tag>
        </div>
      </div>
    </div>

    <div slot="footer" class="dialog-footer" style="text-align: center">
      <el-button @click="hideDialog">取 消</el-button>
      <el-button type="primary" @click="submitForm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { deepClone } from "@/utils/deepClone";

export default {
  name: "chooseLine",
  components: {},
  props: {},
  data() {
    return {
      title: "",
      dialogVisible: false,
      tableData: [],
      lineArr: [], // 已选路线列表
      tableDataHeight: 400, // 表格的动态高度
      formData: {}, // 表格的动态高度
      tableDataIdArr: [], // tableData的id数组
    };
  },
  methods: {
    hideDialog() {
      this.dialogVisible = false;
      this.tableData.forEach((item, index) => {
        item.highlight = false;
      });
      // console.log(this.tableData)
    },

    showDialog(data) {
      setTimeout(() => {
        /* 表格的动态高度 */
        let dialogRightOption = document.querySelector(".dialog-right-option");
        let dialogLeftTableSearch = document.querySelector(
          ".dialog-left-table-search"
        );
        this.tableDataHeight =
          dialogRightOption.clientHeight -
          dialogLeftTableSearch.clientHeight -
          40;
      }, 200);
      this.dialogVisible = true;
      this.title = data.title;
      if (data.data) {
        this.lineArr = data.data;
      }
      // console.log(data, "data");
      this.getTableData(data);
    },

    /**
     * @Event 方法
     * @description: 表格选中事件
     * */
    selectBorderData(row) {
      let index = this.tableDataIdArr.indexOf(row.id);
      if (index !== -1) {
        this.tableData[index].highlight = true;
      }
      if (this.lineArr.findIndex((item) => item.id === row.id) === -1) {
        this.lineArr.push({
          id: row.id,
          lineName: row.lineName,
        });
      } else {
        this.$message.warning("已选择该线路！");
      }
    },

    /**
     * @Event 方法
     * @description: tag 删除事件
     * */
    handleClose(index) {
      let delIndex = this.tableDataIdArr.indexOf(this.lineArr[index].id); // 删除的tag在表格数组中的index
      // console.log(this.lineArr, this.lineArr[index], delIndex); // 已选路线tag, 被删除项tag, xxx
      if (delIndex !== -1) {
        let helpTableData = deepClone(this.tableData);
        this.$set(helpTableData[delIndex], "highlight", false);
        this.tableData = helpTableData;
        this.$forceUpdate();
      }
      this.lineArr.splice(index, 1);
    },

    submitForm() {
      if (this.lineArr.length === 0 || this.lineArr === []) {
        this.$message.error("请先选择线路！");
      } else {
        this.$emit("on-response", this.lineArr);
        this.lineArr = [];
        this.hideDialog();
      }
    },

    getTableData(data) {
      this.tableData = require("./tableData.json").data;
      if (data.data.length !== 0) {
        let idArr = [];
        data.data.forEach((item) => idArr.push(item.id));
        this.tableData.forEach((item, index) => {
          // console.log(idArr, item.id)
          // console.log(idArr.indexOf(item.id), idArr.includes(item.id))
          item.highlight = idArr.includes(item.id) === true;
        });
        // console.log(this.tableData)
      }

      /* 获取 tableDataIdArr */
      this.tableData.forEach((item, index) => {
        this.tableDataIdArr.push(item.id);
      });
    },

    /**
     * @Event 方法
     * @description: 高亮选中的表格行
     * */
    getRowClassName({ row }) {
      return row.highlight ? "highlight-row" : "";
    },
  },
  created() {},
  mounted() {},
};
</script>

