<template>
  <div>
    <backHomeBtn>
      <template>
        <el-input
          style="width: 60%"
          v-model="formData.managementLine"
          disabled
          placeholder=""
        >
          <template slot="append">
            <div class="choose-class" @click="showChooseLine">
              <i class="el-icon-bank-card" /> <span>选择</span>
            </div>
          </template>
        </el-input>

        <div>lineArr：{{ formData.lineId }}</div>
      </template>
    </backHomeBtn>
    <chooseLine @on-response="getLineArr" ref="chooseLine" />
  </div>
</template>

<script>
import chooseLine from "@/components/chooseLine/chooseLine";

export default {
  name: "tableAndTag",
  components: { chooseLine },
  props: {},
  data() {
    return {
      formData: {},
      lineArr: [],
    };
  },
  methods: {
    showChooseLine() {
      let passData = {
        title: "管理线路",
        data: this.lineArr,
      };
      this.$refs.chooseLine.showDialog(passData);
    },

    getLineArr(data) {
      this.lineArr = data;
      console.log(data, this.lineArr);
      this.formData.lineId = JSON.stringify(data); // 保存回显用的数据格式

      let helpArr = [];
      this.lineArr.forEach((item, index) => {
        helpArr.push(item.lineName);
      });
      console.log(helpArr.toString());
      this.$set(this.formData, "managementLine", helpArr.toString());
    },
  },
  created() {},
  mounted() {},
};
</script>

<style lang="scss" scoped>
.choose-class {
  cursor: pointer;
}
</style>
